<template>
    <div>
        <el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
    </div>
</template>
<script>

export default {
    name:"tree",
    data(){
        return{
            data: [],
            defaultProps: {
                children: 'children',
                label: 'target_name'
            }
        }
    },
    methods: {
        handleNodeClick(data) {
            console.log(data);
        }
    },
    mounted(){
        var treeId = this.$route.query.treeId
        console.log(treeId)
        var that = this;
        $.ajax({
            type: "post",
            url: this.baseUrl+"/ydjxzg/mb/wxprojectTree.action",
            data: {
               targetid:treeId 
            },
            dataType: "json",
            success: function (res) {
                console.log(res)
                that.data = res.standardlist
            }
        });
    }
}
</script>
<style scoped>

</style>


