<template>
    <div>
        <el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
    </div>
</template>

<script>
export default {
    name:"wode",
    data(){
        return{
            data: [],
            defaultProps: {
                children: 'children',
                label: 'target_name'
            }
        }
    },
    methods:{
        handleNodeClick(data) {
            console.log(data);
        }
    },
    mounted(){
        var userId = localStorage.getItem("userId")
        var that = this;
        $.ajax({
            type: "post",
            url: this.baseUrl+"/ydjxzg/mb/wxwdjx.action",
            data: {
               user_id:userId 
            },
            dataType: "json",
            success: function (res) {
                console.log(res)
                that.data = res.myscore
            }
        });
    }
}
</script>

<style scoped>

</style>


