<template>
    <div class="biaozhun">
        <div class="tree">
            <div class="tj">
                <ul>
                    <li @click="open"><i class="el-icon-plus"></i> 添加</li>
                    <li @click="open2"><i class="el-icon-edit-outline"></i> 编辑</li>
                    <li @click="open3"><i class="el-icon-delete"></i> 删除</li>
                </ul>
            </div>
            <div class="pre">
                <el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name:"biaozhun",
    data(){
        return{
            data: [],
            defaultProps: {
                children: 'children',
                label: 'name',
                id:"id"
            }
        }
    },
    methods:{
        handleNodeClick(data) {
            console.log(data);
        },
        open() {
            this.$prompt('请输入添加内容', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消'
            }).then(({ value }) => {
            this.$message({
                type: 'success',
                // message: '你的邮箱是: ' + value
                message:"添加成功"
            });
            }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消添加'
            });       
            });
        },
        open2() {
            this.$prompt('请输入编辑内容', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消'
            }).then(({ value }) => {
            this.$message({
                type: 'success',
                // message: '你的邮箱是: ' + value
                message:"编辑成功"
            });
            }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消编辑'
            });       
            });
        },
        open3() {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning',
            center: true
            }).then(() => {
            this.$message({
                type: 'success',
                message: '删除成功!'
            });
            }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            });
            });
        }
    },
    mounted(){
        var that = this;
        $.ajax({
            type: "post",
            url: this.baseUrl+"/ydjxzg/mb/standard.action",
            data: {},
            dataType: "json",
            success: function (res) {
                console.log(res)
                that.data = res.standardlist
            }
        });
    }
}
</script>

<style scoped>
.biaozhun{
    width: 90%;
    height: 100%;
    margin: auto;
}
.tj ul{
    float: left;
    width: 100%;
    height: 100%;
    border-bottom: 0.02rem solid #cccccc;
}
.tj ul li{
    float: left;
    width: 33.3%;
    text-align: center;
    line-height: 1rem;
    font-size: .4rem;
}
.tj ul li i{
    font-size: .3rem;
    color: aliceblue;
    padding: .1rem;
}
.tj ul li:nth-child(1) i{
    background-color: #358FE2;
}
.tj ul li:nth-child(2) i{
    background-color: #7CB349;
}
.tj ul li:nth-child(3) i{
    background-color: #E42739;
}
.pre{
    float: left;
    margin: .3rem 0 0 .5rem;
}
</style>


