<template>
    <div class="dingshi">
        <div class="tabletit">定时生成、发送绩效考核报表</div>
        <div>
            <el-select style="margin-left:.6rem;" v-model="value" placeholder="请选择">
                <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
            </el-select>
            <el-button type="primary">添加</el-button>
        </div>
        <el-table :data="tableData.slice((currpage - 1) * pagesize, currpage * pagesize)" style="width: 100%">
            <el-table-column fixed prop="name" label="姓名" width="150"></el-table-column>
            <el-table-column prop="moblile" label="手机号"></el-table-column>
            <el-table-column prop="dept_name" label="部门"></el-table-column>
            <el-table-column prop="zw" label="职务"></el-table-column>
            <el-table-column prop="fenshu" label="分数"></el-table-column>
        </el-table>
        <el-pagination background 
            layout="prev, pager, next, sizes, total"
            :page-sizes="[5, 10]"
            :page-size="pagesize"
            :total="tableData.length"
            @current-change="handleCurrentChange" 
            @size-change="handleSizeChange"
            style="margin-top:10px; margin-bottom:10px;">
        </el-pagination>
    </div>
</template>

<script>
export default {
    name:"dingshi",
    data(){
        return{
            currentPage: 4,
            pagesize:10,
            currpage:1,
            tableData: [],
            options: [{
            value: '选项1',
            label: '魏老师'
            }, {
            value: '选项2',
            label: '黄江元'
            }, {
            value: '选项3',
            label: '韩兴华'
            }, {
            value: '选项4',
            label: '李文志'
            }, {
            value: '选项5',
            label: '余君'
            }],
            value: ''
        }
    },
    methods: {
      handleSizeChange(val) {
        this.pagesize = val;
      },
      handleCurrentChange(val) {
        this.currpage = val;
      }
    },
    mounted(){
        var that = this;
        var userId = localStorage.getItem("userId")
        $.ajax({
            type: "post",
            url: this.baseUrl+"/ydjxzg/mb/getwxjk.action",
            data: {
                user_id:userId
            },
            dataType: "json",
            success: function (res) {
                console.log(res)
                that.tableData = res.jkryxx
            }
        });
    }
}
</script>

<style scoped>
.dingshi{
    width: 90%;
    height: 100%;
    margin: auto;
    margin-top: .5rem;
}
.tabletit{
    font-size: .5rem;
    text-align: center;
    margin-bottom: .3rem;
}
</style>


