<template>
    <div class="tisheng">
        <div class="tabletit">本部门本月绩效提升排行表</div>
        <el-table :data="tableData.slice((currpage - 1) * pagesize, currpage * pagesize)" style="width: 100%">
            <el-table-column fixed prop="name" label="姓名" width="150"></el-table-column>
            <el-table-column prop="moblile" label="手机号"></el-table-column>
            <el-table-column prop="dept_name" label="部门"></el-table-column>
            <el-table-column prop="zw" label="职务"></el-table-column>
            <el-table-column prop="fenshu" label="分数"></el-table-column>
        </el-table>
        <el-pagination background 
            layout="prev, pager, next, sizes, total"
            :page-sizes="[5, 10]"
            :page-size="pagesize"
            :total="tableData.length"
            @current-change="handleCurrentChange" 
            @size-change="handleSizeChange"
            style="margin-top:10px; margin-bottom:10px;">
        </el-pagination>
    </div>
</template>

<script>
export default {
    name:"tisheng",
    data(){
        return{
            currentPage: 4,
            pagesize:10,
            currpage:1,
            tableData: []
        }
    },
    methods: {
      handleSizeChange(val) {
        this.pagesize = val;
      },
      handleCurrentChange(val) {
        this.currpage = val;
      }
    },
    mounted(){
        var that = this;
        var userId = localStorage.getItem("userId")
        $.ajax({
            type: "post",
            url: this.baseUrl+"/ydjxzg/mb/getwxbmtsjx.action",
            data: {
                user_id:userId
            },
            dataType: "json",
            success: function (res) {
                console.log(res)
                that.tableData = res.bmbyts
            }
        });
    }
}
</script>

<style scoped>
.tisheng{
    width: 90%;
    height: 100%;
    margin: auto;
    margin-top: .5rem;
}
.tabletit{
    font-size: .5rem;
    text-align: center;
    margin-bottom: .3rem;
}
</style>


