<template>
    <div class="xiangqing">
        <div class="xiangmu">
            <div class="top">
                <ul>
                    <li>
                        <i class="el-icon-document"></i>
                        <span>项目详情</span>
                    </li>
                    <li>
                        <p @click="gz(id)" v-if="guanzhu"><i class="el-icon-star-off"></i>关注</p>
                        <p @click="qgz(id)" v-else><i style="color:red;" class="el-icon-star-on"></i>已关注</p>
                    </li>
                    <li>
                        <el-button class="btn1" size="small" @click="handleEdit(bianji)" plain><i class="el-icon-edit-outline"></i>编辑</el-button>
                        <p @click="tree(id)"><i class="el-icon-share"></i> 查看项目树</p>
                    </li>
                </ul>
            </div>
            <div class="bottom">
                <div><span>项目名称：</span><p>{{mc}}</p></div>
                <div><span>紧急程度：</span><p>{{cd}}</p></div>
                <div><span>上级领导：</span><p>{{ld}}</p></div>
                <div><span>上级规划：</span><p>{{gh}}</p></div>
                <div><span>项目时间：</span><p>{{start}}~{{end}}</p></div>
                <div><span>项目经费：</span><p>已消耗{{xh}}万元/预算{{ys}}万元</p></div>
                <div><span>诊改指标：</span><p>{{zb}}</p></div>
                <div><span>诊改标准：</span><p>{{bz}}{{ren}}人</p></div>
                <div><span>负责人：</span><p>{{fzr}}</p></div>
                <div><span>参与人：</span><p>{{cyr}}</p></div>
                <div><span>进度状态：</span><p style="margin-left:1.3rem;"><el-progress :percentage="jd"></el-progress></p></div>
                <div><span>项目简介：</span><p style="line-height:0.5rem;''">{{jj}}</p></div>
            </div>
        </div>
        <div class="fenjie">
            <p><i class="el-icon-share"></i> 本项目的分解任务</p>
            <div class="frw" v-for="(fj,index) in fjs" :key="index">
                <div><span>任务名称：</span><p>{{fj.target_name}}</p></div>
                <div><span>负责人：</span><p>{{fj.manager_name}}</p></div>
                <div><span>经费消耗：</span><p>{{fj.follow}}/{{fj.funds}}</p></div>
                <div style="margin-bottom:.3rem;">
                    <span>审核状态：</span>
                    <p class="shenhe" @click="shenhe(fj)" v-if="fj.check_status == 2">审核通过</p>
                    <p class="shenhe" @click="shenhe(fj)" v-else-if="fj.check_status == 1" style="background-color:blue">已提交审核</p>
                    <p class="shenhe" @click="shenhe(fj)" v-else-if="fj.check_status == 0" style="background-color:#F6CE62">提交未审核</p>
                    <p class="shenhe" @click="shenhe(fj)" v-else-if="fj.check_status == -1" style="background-color:#6DBF67">不能提交审核</p>
                    <p class="shenhe" @click="shenhe(fj)" v-else-if="fj.check_status == -2" style="background-color:red">审核未通过</p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:"xiangqing",
    data(){
        return{
            guanzhu:true,
            fjs:[],
            mc:"",
            cd:"",
            ld:"",
            gh:"",
            start:"",
            end:"",
            xh:"",
            ys:"",
            zb:"",
            bz:"",
            ren:"",
            fzr:"",
            cyr:"",
            jd:"",
            jj:"",
            id:"",
            bianji:""
        }
    },
    methods:{
        handleEdit(bianji){       //编辑
            console.log(bianji)
            var userId = localStorage.getItem("userId")
            if(bianji.creator_id == userId ){
                 this.$router.push({name: 'bianji'});
            }else{
                this.$message({
                    message: '您没有权限操作此项目!',
                    type: 'warning'
                });
            }
        },
        gz(id){
            console.log(id)
            var userId = localStorage.getItem("userId")
            var that = this;
            $.ajax({
                type: "post",
                url: this.baseUrl+"/ydjxzg/mb/targetFollow.action",
                data: {
                    follow:1,
                    follow_id:userId,
                    targetid:id
                },
                dataType: "json",
                success: function (res) {
                    console.log(res)
                    that.guanzhu = false;
                    that.$message.success('关注成功');
                }
            });
        },
        qgz(id){
            console.log(id)
            var userId = localStorage.getItem("userId")
            var that = this;
            $.ajax({
                type: "post",
                url: this.baseUrl+"/ydjxzg/mb/targetFollow.action",
                data: {
                    follow:0,
                    follow_id:userId,
                    targetid:id
                },
                dataType: "json",
                success: function (res) {
                    console.log(res)
                    that.guanzhu = true;
                    that.$message.warning('您已取消关注');
                }
            });
        },
        shenhe(fj){
            console.log(fj)
            this.$router.push({name: 'xiangqing'});
        },
        tree(id){
            console.log(id)
            this.$router.push({
                name: 'tree',
                query: {
                    treeId:id
                }
            })
        }
    },
    mounted(){
        var tid = this.$route.query.targetid
        var userId = localStorage.getItem("userId")
        var bjId = localStorage.getItem("bjId")
        var btn1 = document.getElementsByClassName("btn1");
        // console.log(userId)
        // console.log(bjId)
        if(bjId == userId){
            btn1[0].style.backgroundColor = "#85CE61";
        }else{
            btn1[0].style.backgroundColor = "#cccccc";
        }
        console.log(tid)
        var that = this;
        $.ajax({
            type: "post",
            url: this.baseUrl+"/ydjxzg/mb/targetDetails.action",
            data: {
                user_id:userId,
                targetid:tid
            },
            dataType: "json",
            success: function (res) {
                console.log(res)
                if(res.targetDetails.follow == 1){
                    that.guanzhu = false;
                }else{
                    that.guanzhu = true;
                }
               // console.log(res.targetDetails.id)
                localStorage.setItem("bjId",res.targetDetails.creator_id)
                that.fjs = res.subprojects
                that.bianji = res.targetDetails
                that.mc = res.targetDetails.target_name
                that.cd = res.targetDetails.target_dj
                that.ld = res.targetDetails.upManager
                that.gh = res.targetDetails.target_index
                that.start = res.targetDetails.start_time
                that.end = res.targetDetails.end_time
                that.xh = res.targetDetails.follow
                that.ys = res.targetDetails.funds
                that.zb = res.targetDetails.target_stand_name
                that.bz = res.targetDetails.target_category
                that.ren = res.targetDetails.target_stand_info
                that.fzr = res.targetDetails.manager_name
                that.jd = res.targetDetails.progress
                that.jj = res.targetDetails.target_info
                that.id = res.targetDetails.id
                that.cyr = res.targetDetails.userEntity[0].name
            }
        });
    }
}
</script>
<style scoped>
.xiangqing{
    width: 100%;
    height: 100%;
}
.xiangmu{
    width: 90%;
    height: 100%;
    margin: auto;
    margin-top: .5rem;
}
.xiangmu ul li{
    float: left;
}
.xiangmu ul li:nth-child(2){
    margin-left: .5rem;
}
.xiangmu ul li:nth-child(3){
    float: right;
    margin-right: .5rem;
}
.bottom{
    padding-top: .8rem;
}
.bottom div{
    clear:both;
    width: 100%;
    margin-top: .3rem;
    margin-left: .5rem;
}
.bottom div span{
    width: 20%;
    float: left;
}
.bottom div p{
    width: 80%;
    margin-left: 20%;
}
.fenjie{
    width: 90%;
    height: 100%;
    margin: auto;
    margin-top: .5rem;
    margin-bottom: .5rem;
}
.fenjie div{
    clear:both;
    width: 100%;
    margin-top: .3rem;
    margin-left: .2rem;
}
.fenjie div span{
    width: 20%;
    float: left;
}
.fenjie div p{
    width: 80%;
    margin-left: 20%;
}
.frw{
    border-bottom: 0.01rem solid #cccccc;
}
.top ul li:nth-child(2) p,.top ul li:nth-child(3) p{
    border: 0.01rem solid #cccccc;
    padding: 0.1rem;
}
a{
    text-decoration: none;
    color: black;
}
.shenhe{
    background-color: #85CE61;
    text-align: center;
    color: aliceblue;
    padding: 0.1rem;
    border-radius: 0.1rem;
    width: 2.1rem!important;
}
.btn1{
    background-color: #85CE61;
    padding: 0.2rem;
    font-size: 0.2rem;
    color: #ffffff;
}
</style>



